@import url('../../../../style/common.less');

#FAQ {
    padding-top: .px2vw(60)[@vw]; 
    background-color: @theme-bg-color;
    font-family: @Poppins-SemiBold;
    color: #11CEE5;
    position: relative;

    .top {
        display: flex;

        img {
            width: .px2vw(60)[@vw];
            height: .px2vw(60)[@vw];
            margin-right: .px2vw(30)[@vw];
        }

        span {
            font-size: .px2vw(48)[@vw];
            line-height: .px2vw(48)[@vw];
            font-weight: 600;
        }

    }

    .FAQ-container {
        margin-top: .px2vw(43)[@vw];

        .FAQ-box {
            display: flex;
            font-family: Poppins-SemiBold;
            font-size: .px2vw(32)[@vw];
            color: #11CEE5;
            line-height: .px2vw(32)[@vw];
            font-weight: 600; 
            margin-bottom: .px2vw(60)[@vw];

           
            .FAQ-col2 {
                flex-grow: 1;
                padding-left: .px2vw(15)[@vw];

                &-A {
                    padding-top: .px2vw(30)[@vw];
                    font-size: .px2vw(28)[@vw];  
                    color: #FFFFFF;  
                    line-height: 2em;
                    height: 0;
                    overflow: hidden;
                }
                .A-open {
                    height: 100%;
                    p {
                        margin-bottom: 0;
                    }
                }
                .A-close {
                    height: 0;
                }
            }
            .FAQ-col3 {
                img {
                    cursor: pointer;
                    width: .px2vw(24)[@vw];
                    height: .px2vw(14)[@vw];
                }
            }
        }
    }
}



@keyframes open {
    0% {
        height:  0;
    }
    100% {
        height: 100%;
    }
}

@-webkit-keyframes open {
    0% {
        height:  0;
    }
    100% {
        height: 100%;
    }
}

@keyframes close {
    0% {
        height:  100%;
    }
    100% {
        height: 0%;
    }
}

@-webkit-keyframes close {
    0% {
        height:  100%;
    }
    100% {
        height: 0;
    }
}