@import url('../../../../style/common.less');

#tiandi {
    background-color: #000;
    padding: .px2vw(60)[@vw] .px2vw(40)[@vw] 0;
    text-align: center;

    .intro-title {
        margin-bottom: .px2vw(40)[@vw];
        font-family: @Poppins-SemiBold;
        font-size: .px2vw(48)[@vw];
        color: #11CEE5;
        height: .px2vw(48)[@vw];
        line-height: .px2vw(48)[@vw];
        font-weight: 600;
    }

    .intro-content {
        font-family: @Poppins-Regular;
        font-size: .px2vw(28)[@vw];
        p {
            margin-bottom: 0.5em;
        }
    }
    
    .bank-main {

        .bank-divider {
            width: .px2vw(606)[@vw];
            height: .px2vw(50)[@vw];
            padding: .px2vw(40)[@vw] 0;
            margin: 0 auto;
        }

        .bank-products {
            display: flex;
            justify-content: space-between;
            align-content: space-around;
            flex-wrap: wrap;
            // height: 700px;

            .prod-card {
                // width: 278px;
                margin-bottom: .px2vw(40)[@vw];
                .prod-img {
                    width: .px2vw(320)[@vw];
                    height: .px2vw(320)[@vw];
                }
                .prod-footer {
                    display: flex;
                    
                    .prod-left {
                        flex-grow: 1;
                        font-size: .px2vw(32)[@vw];
                        line-height: .px2vw(32)[@vw];

                        span {
                            display: block;
                            text-align: left;
                        }
                        .prod-name {
                            height: .px2vw(32)[@vw];
                            font-family: @Poppins-SemiBold;
                            color: #11CEE5;
                            font-weight: 600;
                            margin: .px2vw(20)[@vw] 0 .px2vw(12)[@vw] 0;                        }

                        .prod-id {
                            height: .px2vw(24)[@vw];
                            font-size: .px2vw(24)[@vw];
                            font-family: @Poppins-Regular;
                            color: #FFFFFF;
                        }
                    }

                    .prod-right {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        flex-grow: 0;

                        .prod-icon {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            width: .px2vw(50)[@vw];
                            height: .px2vw(50)[@vw];
                            flex-grow: 0;
                            background-color: #11CEE5;
                            cursor: pointer;

                            img {
                                width: .px2vw(26)[@vw];
                                height: .px2vw(26)[@vw];
                            }
                        }
                    }
                    
                }
            }
        }
    }
}