@import url('../../../../style/common.less');

#team {
    background-color: @theme-bg-color;
    padding: .px2vw(60)[@vw] 0;
    
    .team-container {
        margin: 0 auto;

        .team-divider {
            display: flex;
            justify-content: center;
            align-items: center;

            .divider-left,
            .divider-right {
                height: .px2vw(14)[@vw];
                width: .px2vw(220)[@vw];
                align-self: flex-end;
                margin-bottom: .px2vw(12)[@vw];
            }

            .divider-center {
                margin: 0 .px2vw(30)[@vw];
                display: flex;
                justify-content: center;
                align-items: center;

                img {
                    width: .px2vw(60)[@vw];
                    height: .px2vw(60)[@vw];
                    margin-right: .px2vw(30)[@vw];
                }

                span {
                    display: block;
                    height: .px2vw(48)[@vw];;
                    font-family: @Poppins-SemiBold;
                    font-size: .px2vw(48)[@vw];
                    color: #11CEE5;
                    line-height: .px2vw(48)[@vw];
                    font-weight: 600;
                }
            }


        }
        
        .team-avatar {
            
            &-box {
                position: relative;

                img {
                    display: block;
                    width: .px2vw(480)[@vw];
                    height: .px2vw(480)[@vw];
                }

                .team-avatar-desc {
                    display: flex;
                    justify-content: center;
                    position: absolute;
                    bottom: 0;
                    width: .px2vw(327)[@vw];
                    padding: .px2vw(30)[@vw] 0;
                    opacity: 0.9;
                    background: #11CEE5;
                    
                    .desc-left {
                        width: .px2vw(30)[@vw];
                        height: .px2vw(30)[@vw];
                    }

                    .desc-right {
                        padding-left: .px2vw(17)[@vw];
                        
                        span {
                            display: block;
                        }
                        
                        .desc-title {
                            height: .px2vw(28)[@vw];
                            font-family: @Poppins-SemiBold;
                            font-size: .px2vw(28)[@vw];
                            color: #000000;
                            line-height: .px2vw(28)[@vw];
                            font-weight: 600;
                        }

                        .desc-extra {
                            height: .px2vw(20)[@vw];
                            font-family: PingFangSC-Regular;
                            font-size: .px2vw(20)[@vw];
                            color: #333333;
                            line-height: .px2vw(20)[@vw];
                            font-weight: 400;
                            margin-top: .px2vw(10)[@vw];
                        }
                    }
                }
            }

            .avatar-queen {
                padding-top: .px2vw(60)[@vw];
                padding-left: .px2vw(60)[@vw];

                .team-avatar-desc {
                     left: .px2vw(293)[@vw];
                     border-radius: .px2vw(30)[@vw] 0px 0px 0px;

                }
            }

            .avatar-jade {
                padding-top: .px2vw(40)[@vw];
                padding-left: .px2vw(210)[@vw];

                .team-avatar-desc {
                    left: .px2vw(131)[@vw];
                    border-radius: 0px .px2vw(30)[@vw] 0px 0px;

                }
            }
        }
    }

}