@import url('../../../../style/common.less');

.header {
    position: fixed;
    top: 0;
    height: .px2vw(120)[@vw];
    width: 100%;
    z-index: 1;
    padding: .px2vw(30)[@vw] 0;
    background-color: @theme-bg-blue-black;
    box-sizing: border-box;

    .top-nav {
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        img {
            width: .px2vw(252)[@vw];
            height: .px2vw(60)[@vw];
            cursor: pointer;
        }

        &-right {
            cursor: pointer;
            img {
                width: .px2vw(34)[@vw];
                height: .px2vw(28)[@vw];
            }

        }
    }

    &-drawer {
        height: 100vmax;
        width: .px2vw(610)[@vw];
        position: absolute;
        top: 0;
        right: 0;
        background-color: @theme-bg-blue-black;
        z-index: 999;

        text-align: right;
        font-family: @Poppins-SemiBold;
        font-size: .px2vw(32)[@vw];
        line-height: .px2vw(32)[@vw];
        font-weight: 600;
        color: #FFFFFF;
         
        .drawer-bg {
            position: absolute;
            bottom: 0;
            width: 100%;
            height: .px2vw(644)[@vw];;
            background: url('https://eternalverse.s3.us-east-2.amazonaws.com/fire_opacity.png') no-repeat bottom / 100%;
            z-index: -1;
        }
        .drawer-close {
            height: .px2vw(120)[@vw];
            padding: .px2vw(44)[@vw] .px2vw(40)[@vw] .px2vw(44)[@vw] 0;
            box-sizing: border-box;
            img {
                display: block;
                float: right;
                width: .px2vw(32)[@vw];
                height: .px2vw(32)[@vw];
            }
        }

        .nav-anch {
            padding-right: .px2vw(40)[@vw];
            margin-top: .px2vw(36)[@vw];

            &-container {
                margin-bottom: .px2vw(60)[@vw];
                height: .px2vw(32)[@vw];
               
            }
           
             .anch-active {
                color: #11CEE5;
            }

            .lang-btn {
                display: flex;
                align-items: center;
                justify-content: flex-end;
                span {
                    margin-right: .px2vw(20)[@vw];
                }
                
                .lang-arrow-up {
                    transform: rotate(-180deg);
                    // transition: 0.5s ease-in-out;
                }
            }
             .lang-item {
                margin-top: .px2vw(60)[@vw];;
            }
        }  
    }
}