@import url('../../../../style/common.less');

#home {
    width: 100%;
    position: relative;
    background-color: @theme-bg-blue-black;
    margin-top: .px2vw(120)[@vw];
    padding-top: .px2vw(40)[@vw];
    padding-bottom: .px2vw(20)[@vw];
    overflow: hidden;
    
    .intro-bg {
        position: absolute;
        bottom: .px2vw(-336)[@vw];
        left: .px2vw(-260)[@vw];
        width: 160%;
    }


    .intro-card-container {
        display: flex;
        justify-content: flex-end;
        
        .intro-card {
            position: relative;

            &-title {
                position: relative;

                .title-font {
                    display: block;
                    width: .px2vw(400)[@vw];
                    height: .px2vw(94)[@vw];
                    // background-image: url(../../../../assets/images/logo.png);
                    // background: url('src/assets/images/logo.png') no-repeat center/100%;
                }

                .title-font-bg {
                    position: absolute;
                    display: block;
                    height: .px2vw(104)[@vw];
                    width: .px2vw(656)[@vw];
                    top: .px2vw(32)[@vw];;
                }
                
            }

            &-content {
                margin-top: .px2vw(30)[@vw];
                margin-bottom: .px2vw(254)[@vw];
                font-family: @Poppins-Regular;
                font-size: .px2vw(28)[@vw];
                color: #FFFFFF;
            }

            &-link {
                // width: 380px;
                // height: 140px;
                display: flex;
                justify-content: space-between;
                align-content: space-between; 
                flex-wrap: wrap;

                .website-card {
                    display: flex;
                    align-items: center;
                    width: .px2vw(295)[@vw];
                    height: .px2vw(80)[@vw];
                    border-radius: .px2vw(8)[@vw];
                    margin-bottom: .px2vw(40)[@vw];

                    font-family: @Poppins-SemiBold;
                    font-size: .px2vw(28)[@vw];
                    font-weight: 600;
                    color: #010210;
                    text-align: center;
                    background-color: #11CEE5;
                    cursor: pointer;
                    
                    .card-left {
                        flex-grow: 2;
                        border-right: 1px solid #010210;
                    }
                    .card-right {
                        flex-grow: 1;
                        img {
                            width: .px2vw(28)[@vw];
                            height: .px2vw(28)[@vw];
                        }
                    }
                }
            }
        }
    }
}