@import url('../../../../style/common.less');

.footer {
    padding: .px2vw(40)[@vw] 0 .px2vw(60)[@vw] 0;
    font-size: .px2vw(24)[@vw];
    line-height: .px2vw(24)[@vw];
    font-family: @Poppins-Regular;
    font-weight: 400;
    background-color: #111322;

    &-logo {
        width: .px2vw(200)[@vw];
        height: .px2vw(48)[@vw];
    }

    &-webintro {
        margin: .px2vw(23)[@vw] 0 .px2vw(40)[@vw] 0;

        span {
            color: #FFFFFF;
            font-weight: 400;
            line-height: 1.5em;
        }
    }

    &-copyright {
        margin-top: .px2vw(40)[@vw];
        height: .px2vw(24)[@vw];
        color: #11CEE5;
        line-height: .px2vw(24)[@vw];
    }

    &-link {
        display: flex;

        .link-box {
            width: .px2vw(60)[@vw];
            height: .px2vw(60)[@vw];
            display: flex;
            justify-content: center;
            align-items: center;
            margin-right: 10px;
            border: 1px solid rgba(17,206,229,1);
            cursor: pointer;

            img {
                width: .px2vw(30)[@vw];
                height: .px2vw(30)[@vw];
            }
        }
    }
}