@import url('../../../../style/common.less');

#roadmap {
    position: relative;
    width: 100%;
    padding: .px2vw(60)[@vw] 0;
    background-color: @theme-bg-blue-black;
    font-family: @Poppins-SemiBold;
    color: #11CEE5;
    
    .map-bg {
        position: absolute;
        right: 0;
        bottom: 0;
        img {
            width: .px2vw(516)[@vw];
            height: .px2vw(620)[@vw];
        }
    }

    .road-container {
        .road-title {
            display: flex;
            align-items: center;
            margin-bottom: .px2vw(60)[@vw];
            img {
                width: .px2vw(60)[@vw];
                height: .px2vw(60)[@vw];
                margin-right: .px2vw(30)[@vw];
            }
            span {
                height: .px2vw(48)[@vw];
                font-size: .px2vw(48)[@vw];
                line-height: .px2vw(48)[@vw];
                font-weight: 600;
            }
        }

        .road-footerprint {
            display: flex;
            min-height: .px2vw(114)[@vw];
            font-size: .px2vw(22)[@vw];

            .footer-col1 {
                line-height: .px2vw(32)[@vw];
                height: .px2vw(32)[@vw];
            } 

            .footer-col2 {
                padding: 0 .px2vw(32)[@vw];

                .col2-img-container {
                    height: .px2vw(32)[@vw];
                    width: .px2vw(32)[@vw];
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    img {
                        width: .px2vw(30)[@vw];
                        height: .px2vw(30)[@vw];
                        transform: rotate(-90deg);
                    }
                }
                

                &::after {
                    content: '';
                    display: block;
                    height: 60%;
                    margin: 6px auto;
                    width: 1px;
                    background-color: #11CEE5;
                }
            }

            .footer-col3 {
                .desc-line {
                    display: flex;
                    // line-height: 22px;
                    // margin-bottom: 30px;
                    .desc-line-img {
                        height: .px2vw(32)[@vw];
                        display: flex;
                        align-items: center;

                        img {
                            width: .px2vw(24)[@vw];
                            height: .px2vw(24)[@vw];
                            flex-shrink: 0;
                        }
                    }
                    
                    .footer-desc {
                        padding-left: .px2vw(20)[@vw];
                        color: #11CEE5;
                        margin-top: 0;
                    }
                }
                
            }

        }
        .road-footerprint:last-child {
            .footer-col2::after {
                display: none;
            }
        }
    }
}